.banner{

  &__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 200px;
    padding: 0 60px 0 60px;

    background: #FFFFFF;
    border-radius: 8px;
    &-mobile{
      display: none;
    }
  }
  &__img{
    max-width: 455px;
    max-height: 300px;
    flex-shrink: 0;

    display: flex;
    align-items: center;
    justify-content: center;
    img{
      height: auto;

      max-height: 300px;

      object-fit: contain;
    }
  }
  &__content{
    max-width: 550px;

    margin: 0 15px;
  }
  &__title{
    font-family:'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 120%;
    color: #2A2A2A;
    span{
      font-size: 28px;
      font-weight: 600;
    }
  }
  &__text{
    margin-top: 4px;

    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    line-height: 115%;

    letter-spacing: -0.01em;

    color: #23222F;
  }
  &__footer{
    margin-left: auto;
  }
  &__btn{
    width: 150px;
    height: 55px;

    padding: 0 15px;

    border: 1px solid #2956FF;
    border-radius: 25px;

    font-weight: 600;
    font-size: 18px;
    letter-spacing: 0.02em;

    color: #2956FF;
  }
}

@media screen and (max-width: 991px) {
  .banner{
    &__wrapper{
      flex-direction: column;
      height: auto;
      align-items: flex-start;

      padding: 15px;
    }
    &__footer{
      margin-left: 0;

      margin-top: 10px;
    }
    &__title{
      font-size: 20px;
    }
    &__img{
      width: 200px;
      height: 135px;

      transform: translateY(-30px);
      img{
        height: 100%;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .banner{
    margin: 20px 0 35px 0;
    &__text{
      margin-top: 0;
    }
  }
}