.single-article{
  &__wrapper{
    width: 100%;
    max-width: 780px;

    margin: 0 auto;


    background: #FFFFFF;
    border-radius: 8px;

    position: relative;

    overflow: hidden;
  }
  &__header{
    width: 100%;
  }
  &__img{
    height: 250px;
    &-item{
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }
  &__info{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-text{
      font-family: "Acrom", sans-serif;
      font-size: 14px;

      color: #898989;
    }
  }
  &__content{
    padding: 35px 30px;
  }
  &__title{
    font-family: 'Merriweather', sans-serif;
    font-weight: 700;
    @include fluid-type($min_width, $max_width, 32px, 42px);
    line-height: 110%;
    color: #2A2A2A;

    margin-top: 15px;
  }
  &__desc{
    margin-top: 15px;

    &-facts{
      display: grid;
      grid-template-columns: 75px auto;

      border-radius: 15px;
      overflow: hidden;

      margin: 30px 0;
      &-img{
        flex-shrink: 0;
        width: 100%;
        height: 100%;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &-content{
        background-color: #f5f5fa;

        padding: 15px 20px;
      }
      &-title{
        color:#202020;
        font-weight: 700;
        font-size: 22px;
        font-family: 'Montserrat', sans-serif;
        margin-bottom: 15px;
      }
      &-text{
        color:#6e6e6e;

        margin-bottom: 10px;
        font-family: 'Montserrat', sans-serif;
        font-weight: 400;
        font-size: 18px;
        line-height: 125%;
      }
    }

    p {
      margin-bottom: 10px;
      font-family: 'Montserrat', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 125%;

      color: #000000;
    }
    blockquote{
      font-family: 'Acrom', sans-serif;
      font-weight: 500;
      font-size: 18px;
      line-height: 125%;

      margin: 15px 0;

      background: #F5F5FB;

      padding: 20px;
    }
  }
  &__closed{
    position: absolute;

    bottom: 0;
    left: 0;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    background:linear-gradient(to top, #fff 30%, rgba(#fff,0));


    padding: 300px 0 50px 0;
    &-title{
      color:#202020;

      text-align: center;
      font-family: 'Montserrat', sans-serif;
      font-weight: 700;
      font-size: 26px;

      max-width: 350px;
    }
    &-btn{
      margin-top: 40px;

      width: max-content;

      padding: 15px;
    }
  }
}

@media screen and (max-width: 900px) {
  .single-article{
    &__img{
      height: auto;
    }
  }
}

@media screen and (max-width: 600px) {
  .single-article{
    &__desc{
      blockquote{
        padding: 13px 15px;
      }
    }
  }
}