.card{
  &-grid{
    &.material-grid{
      .card-item__title{
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
      }
    }
    &__wrapper{
      display: grid;
      grid-template-columns: repeat(4,minmax(0,1fr));
      gap: 40px 30px;
      &_small{
        gap: 30px;
        grid-template-columns: repeat(3,minmax(0,1fr));
      }
    }
    &__item{
      &_column{
        &-medium{
          grid-column-end: span 2;
        }
      }
    }
    &__footer{
      margin-top: 20px;

      display: flex;
        align-items: flex-end;
      justify-content: flex-end;

      flex-direction: column;
      &_margin{
        &-small{
          margin-top: 15px;
        }
        &-big{
          margin-top: 45px;
        }
      }
      &_position{
        &-center{
          justify-content: center;
          align-items: center;
        }
      }
      &-img{
        width: 100%;
      }
    }
    &__link{
      font-family: 'Montserrat', sans-serif;


      display: flex;
      align-items: center;
      justify-content: center;

      font-weight: 400;
      font-size: 12px;
      letter-spacing: 0.02em;

      color: #2956FF;

      height: 30px;

      width: max-content;

      padding: 0 15px;

      border: 1px solid #2956FF;
      border-radius: 25px;
      &-big{
        height: 45px;
        width: 175px;
      }
    }
    &__btn{
      max-width: 195px;
      height: 45px;
      &-mobile{
        display: none;
      }
    }
  }
  &-item{
    height: 100%;
    &_big{
      grid-column-end: span 2;
    }
    &__wrapper{
      position: relative;

      background: #fff;
      border-radius: 8px;

      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      height: 100%;
      &_row{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        height: 100%;

        box-shadow: 0px 3px 15px rgba(52, 26, 26, 0.05);
      }
    }
    &__header{
      display: flex;
      flex-direction: column;
      justify-content: center;

      width: 100%;


      position: relative;

      border-top-right-radius: 8px;
      border-top-left-radius: 8px;
      overflow: hidden;
      &_size{
        &-small{
          width: auto;
          height: 100%;
          flex-shrink: 0;

          border-radius: 8px 0 0 8px;
        }
      }
      &-stars{
        position: absolute;
        left: 9px;
        top: 7px;
      }
      &-footer{
        position: relative;
        left: 0;
        bottom: 0;

        display: flex;
        align-items: center;
        justify-content: flex-start;

        padding: 12px 25px;
      }
    }
    &__stars{
      display: grid;
      grid-template-columns: repeat(5,minmax(0,12px));
      gap: 1px;
    }
    &__star{
      width: 12px;
      height: 12px;
      &-img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &__category{
      width: 20px;
      height: 20px;

      display: flex;
      align-items: center;
      justify-content: center;
      &-icon{
        width: 18px;
        height: 18px;
      }
    }
    &__date{
      margin-left: auto;

      font-family: 'Montserrat', sans-serif;
      font-size: 12px;
      font-weight: 500;
      color:#6a6a6a;
    }
    &__tags{
      margin-left: -5px;

      margin-bottom: 8px;
    }
    &__tag + &__tag{
      margin-left: 4px;
    }
    &__tag{
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      height: auto;
      flex-wrap: wrap;
      border-radius: 10px;
      &-item{
        display: flex;
        align-items: center;
        justify-content: center;

        padding: 6px 8px;

        margin:2.5px;

        background: #EDEDFD;
        border-radius: 20px;
      }
      &-text{
        font-family: 'Montserrat', sans-serif;
        font-size: 11px;
        font-weight: 400;
        letter-spacing: 0.02em;

        color:#000000;
      }
    }
    &__img{
      width: 145px;
      height: 100%;

      flex-shrink: 0;
      object-fit: cover;
      &_size{
        &-full{
          width: 100%;
          height: 115px;
          object-fit: cover;
        }
      }
    }
    &__content{
      padding: 0px 25px 20px 25px;
      &_size{
        &-small{
          padding:13px 25px;

          display: flex;
          flex-direction: column;
          justify-content: center;
          height: 100%;
        }
      }
    }
    &__title{
      font-family: "Montserrat", sans-serif;
      font-weight: 700;
      color:#2A2A2A;
      font-size: 18px;
      line-height: 1.2;
      &_position{
        &-center{
          text-align: center;
        }
      }
    }
    &__text{
      font-family: 'Montserrat', sans-serif;
      color:#000000;
      font-size: 12px;
      line-height: 14px;

      margin-top: 5px;

    }
    &__btn{
      width: 90px;
      height: 30px;

      margin-top: 8px;

      border-radius: 20px;


      text-transform: uppercase;
      font-weight: 600;
    }
  }
}

@media screen and (max-width: 1200px) {
  .card {
    &-grid {
      &__wrapper {
        grid-template-columns: repeat(2,minmax(0,1fr));
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .card {
    &-grid {
      &__wrapper {
        grid-template-columns: repeat(1,minmax(0,1fr));
        &_small{
          gap: 20px;
        }
      }
      &__btn{
        &-mobile{
          display: flex;
        }
      }
    }
    &-item{
      &_big{
        grid-column-end: span 1;
      }
      &__title{
        text-align: left;

        font-size: 16px;
      }
      &__text{
        font-size: 13px;
      }
      &__content{
        &_size{
          &-small{
            padding: 15px;
          }
        }
      }
    }
  }
  .card{
    &-item{
      &__category{
        width: 23px;
        height: 23px;
       &-icon{
         height: 23px;
         width: 23px;

         flex-shrink: 0;
       }
      }
    }
  }
}


@media screen and (max-width: 475px) {
  .test{
    &-grid{
      .card{
        &-grid{
          &__item{
            display: none;
            &:nth-child(1),&:nth-child(2),&:nth-child(3){
              display: block;
            }
          }
        }
      }
    }
  }
  .card {
    &-grid {
      &.material-grid{
        .card-item__title{
          font-size: 19px;
        }
      }
      &__wrapper {
        grid-template-columns: minmax(0,1fr);

        gap: 32px;
        &_small{
          gap: 15px;
        }
      }
      &__footer{
        &-img{
          margin-top: 20px;
        }
      }
      &__link{
        width: 100%;
        height: 45px;

        margin-top: 10px;
      }
    }
  }
}
