.test {
  &__wrapper {
    max-width: 790px;

    margin: 0 auto;

    background: #FFFFFF;
    border-radius: 8px;

    padding: 20px 30px 60px 30px;
  }

  &__btn {
    max-width: 185px;
    height: 55px;

    font-weight: 600;
  }

  &-wrapper {
    &-answer {
      &.hidden {
        display: none;
      }
    }

    &-info {
      &.hidden {
        display: none;
      }
    }

    &-result {
      display: none;

      &.active {
        display: block;
      }
    }

    &__header {
      &-title {
        font-family: 'Acrom', sans-serif;
        font-weight: 500;
        font-size: 16px;
        line-height: 125%;
        color: #989898;
      }
    }

    &__content {
      margin-top: 15px;
    }

    &__footer {
      margin-top: 50px;
    }
  }

  &-content {
    &__header {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
    }

    &__steps {
      flex-shrink: 0;
      font-family: 'Acrom', sans-serif;
      font-weight: 700;
      font-size: 16px;
      color: #B9BBC9;

      margin-left: 55px;
    }

    &__title {
      font-family: 'Montserrat', sans-serif;
      font-weight: 700;
      font-size: 22px;
      line-height: 110%;
      color: #2A2A2A;
    }

    &__answers {
      margin-top: 40px;
    }

    &__result {
      display: none;

      &.active {
        display: block;
      }
    }
  }

  &-input {
    &__wrapper {
      padding: 10px;

      display: flex;
      align-items: center;

      position: relative;

      &:hover {
        cursor: pointer;

        .test-input__bg {
          opacity: 1;
        }
      }
    }

    &__item {
      display: none;

      &:checked ~ .test-input__checkmark {
        background: #5F2EEA;

        &::before {
          opacity: 1;
        }
      }

      &:checked ~ .test-input__bg {
        opacity: 1;
      }
    }

    &__bg {
      position: absolute;
      left: 0;
      top: 0;

      width: 100%;
      height: 100%;
      display: block;

      border-radius: 8px;

      background-color: #FAF5D9;
      z-index: 1;

      opacity: 0;
      transition: .3s opacity;
    }

    &__checkmark {
      width: 32px;
      height: 32px;

      display: block;
      border-radius: 50%;

      background: #D9DBE9;

      position: relative;
      z-index: 2;

      flex-shrink: 0;

      transition: 0.3s background-color;

      &::before {
        content: '';

        position: absolute;
        top: calc(50% - 8px);
        left: calc(50% - 8px);

        width: 16px;
        height: 16px;

        border-radius: 50%;

        background: #F7F7FC;

        opacity: 0;

        transition: .3s opacity;
      }
    }

    &__text {
      margin-left: 10px;

      font-family: 'Montserrat', sans-serif;
      font-size: 16px;
      line-height: 18px;

      position: relative;
      z-index: 2;
    }
  }

  &-answers {
    &__block {
      & + & {
        margin-top: 12px;
      }
    }
  }

  &-result {
    &__header {
      &-title {
        font-family: 'Acrom', sans-serif;
        font-weight: 700;

        font-size: 17px;
        color: #989898;
      }

      &-img {
        height: 225px;
        width: 100%;

        object-fit: cover;

        margin-top: 15px;

        border-radius: 10px;
      }
    }

    &__info {
      margin-top: 20px;

      &-title {
        font-family: 'Acrom', sans-serif;
        font-weight: 700;
        font-size: 17px;

        color: #989898;
      }

      &-text {
        font-family: 'Acrom', sans-serif;
        font-weight: 400;
        font-size: 16px;

        color: #989898;

        margin-top: 5px;
      }
    }

    &__content {
      margin-top: 15px;
    }

    &__title {
      font-family: 'Acrom', sans-serif;
      font-weight: 700;

      font-size: 34px;

      color: #4F4F4F;
    }

    &__description {
      margin-top: 5px;

      p + p {
        margin-top: 15px;
      }

      p {
        font-family: 'Acrom', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 1.25;
        color: #000000;
      }

      img {
        height: 220px;

        object-fit: cover;

        border-radius: 8px;
      }

      &-block + &-block {
        margin-top: 30px;
      }

      &-blockquote {
        padding: 22px 42px;

        &-first {
          background: #E7DFFF;
          border-radius: 10px;
        }

        &-second {
          background: #FFEBFF;
          border-radius: 10px;
        }

        p {
          font-weight: 500;
        }
      }
    }

    &__footer {
      margin-top: 30px;

      display: flex;
      align-items: center;
      justify-content: flex-end;
    }

    &__btn + &__btn {
      margin-left: 30px;
    }

    &__btn {
      font-weight: 700;
      max-width: 185px;
      height: 40px;

      border-radius: 20px;
    }
  }

  &-info {
    &__header {
      &-title {
        font-family: 'Acrom', sans-serif;
        font-weight: 500;
        font-size: 16px;
        line-height: 125%;
        color: #989898;
      }

      &-img {
        height: 220px;

        margin-top: 20px;
      }
    }

    &__content {
      margin-top: 10px;
    }

    &__title {
      font-family: 'Acrom', sans-serif;
      font-weight: 700;
      font-size: 34px;
      color: #2A2A2A;
    }

    &__sub-title {
      font-family: 'Acrom', sans-serif;
      font-weight: 500;
      font-size: 18px;
      line-height: 125%;
      color: #989898;

      margin-top: 20px;
    }

    &__description {
      margin-top: 20px;

      p + p {
        margin-top: 15px;
      }

      p {
        font-family: 'Acrom', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 1.25;
        color: #000000;
      }
    }

    &__footer {
      display: flex;
      justify-content: flex-end;

      margin-top: 20px;
    }

    &__btn {
      font-weight: 700;
      max-width: 185px;
      height: 40px;
      border-radius: 20px;

      text-transform: uppercase;
    }
  }
}

@media screen and (max-width: 600px) {
  .test {
    &__wrapper {
      padding: 10px;
    }

    &-result {
      &__description {
        &-blockquote {
          padding: 20px 30px;
        }

        img{
          height: auto;
        }
      }
      &__header{
        &-img{
          height: auto;
        }
      }
    }

    &-info{
      &__title{
        font-size: 30px;
        line-height: 1.1;
      }
      &__header{
        &-img{
          height: auto;
        }
      }
    }

    &-content {
      &__steps {
        margin-left: 10px;
      }

      &__title {
        font-size: 18px;
      }

      &__answers {
        margin-top: 20px;
      }
    }

    &-wrapper {
      &__footer {
        display: flex;
        align-items: center;
        justify-content: center;

        padding-bottom: 10px;
      }
    }

    &-result {
      &__title{
        font-size: 30px;
        line-height: 1.1;
      }
      &__footer {
        align-items: center;
        justify-content: space-between;
      }

      &__btn + &__btn {
        margin-left: 10px;
      }

      &__btn {
        font-size: 13px;
      }
    }
  }
}