.profile{
  &__wrapper{
    width: 100%;
    max-width: 780px;

    overflow: hidden;
  }
  &-wrapper{
    &__block{
      width: 100%;
      background: #FFFFFF;
      border-radius: 8px;

      position: relative;

      padding: 15px 23px;
      & + &{
        margin-top: 15px;
      }
    }
  }
  &-block{
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &-title{
        color:#888888;
        font-size: 16px;
      }

      &.trigger-link::after{
        content: '';
        width: 15px;
        height: 15px;
        display: block;

        transform: rotate(180deg);

        background: url("../img/block-arrow.png") center center/contain no-repeat;
      }
      &.active::after{
        transform: rotate(0);
      }
    }
    &__header + &__content{
      margin-top: 30px;
    }
    &__content{
      &-title{
        font-weight: 700;
        color:#202020;
        font-size: 18px;

        margin-bottom: 10px;
      }
      &-text{
        color:#202020;
        font-size: 18px;
        & + &{
          margin-top: 5px;
        }
      }
      &-text + &-btn{
        margin-top: 15px;
      }
      &-btn + &-text{
        margin-top: 15px;
      }
      &-btn{
        max-width: 220px;
        height: 30px;
      }
      &-block{
        & + &{
          margin-top: 30px;
        }
      }
    }
    &__input{
      &-content{
        display: flex;
        align-items: center;

        margin-top: 10px;
      }
      &-item{
        border:1px solid #a7a7a7;
        border-radius: 5px;
        margin: 0;
        outline: none;

        height: 25px;
        width: 180px;
      }
      &-status{
        margin-left: 10px;

        font-size: 14px;
        &_success{
          color:#127d12;
        }
        &_error{
          color:#d32f2e;
        }
      }
    }
  }
  &-table{
    border:2px solid #dddddd;
    border-radius: 10px;
    width: max-content;
    &__row{
      display: grid;
      grid-template-columns: 120px 220px 120px 120px;

      border-bottom: 2px solid #dddddd;

      &:last-child{
        border-bottom: none;
      }
    }
    &__column{
      font-size: 18px;

      padding: 10px 15px;

      border-left: 2px solid #dddddd;
      &:first-child{
        border-left: none;
      }
    }
    &__title{

    }
  }
}


@media screen and (max-width: 767px) {
  .profile{
    &-table{
      width: 100%;
      overflow: hidden;
      &__row{
        overflow: scroll;
      }
      &__column{
        font-size: 14px;
      }
    }
  }
}