}

.test__btn {
  max-width: 185px;
  height: 55px;
  font-weight: 600;
}

.test-wrapper-answer.hidden {
  display: none;
}

.test-wrapper-info.hidden {
  display: none;
}

.test-wrapper-result {
  display: none;
}

.test-wrapper-result.active {
  display: block;
}

.test-wrapper__header-title {
  font-family: 'Acrom', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 125%;
  color: #989898;
}

.test-wrapper__content {
  margin-top: 15px;
}

.test-wrapper__footer {
  margin-top: 50px;
}

.test-content__header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.test-content__steps {
  flex-shrink: 0;
  font-family: 'Acrom', sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #B9BBC9;
  margin-left: 55px;
}

.test-content__title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 110%;
  color: #2A2A2A;
}

.test-content__answers {
  margin-top: 40px;
}

.test-content__result {
  display: none;
}

.test-content__result.active {
  display: block;
}

.test-input__wrapper {
  padding: 10px;
  display: flex;
  align-items: center;
  position: relative;
}

.test-input__wrapper:hover {
  cursor: pointer;
}

.test-input__wrapper:hover .test-input__bg {
  opacity: 1;
}

.test-input__item {
  display: none;
}

.test-input__item:checked ~ .test-input__checkmark {
  background: #5F2EEA;
}

.test-input__item:checked ~ .test-input__checkmark::before {
  opacity: 1;
}

.test-input__item:checked ~ .test-input__bg {
  opacity: 1;
}

.test-input__bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  border-radius: 8px;
  background-color: #FAF5D9;
  z-index: 1;
  opacity: 0;
  transition: .3s opacity;
}

.test-input__checkmark {
  width: 32px;
  height: 32px;
  display: block;
  border-radius: 50%;
  background: #D9DBE9;
  position: relative;
  z-index: 2;
  flex-shrink: 0;
  transition: 0.3s background-color;
}

.test-input__checkmark::before {
  content: '';
  position: absolute;
  top: calc(50% - 8px);
  left: calc(50% - 8px);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #F7F7FC;
  opacity: 0;
  transition: .3s opacity;
}

.test-input__text {
  margin-left: 10px;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 18px;
  position: relative;
  z-index: 2;
}

